@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
body{
  margin:0;
  width: 100%;
  height: 100%;
  background-image: url("https://i.pinimg.com/originals/82/32/15/8232157f8d4e09717bf71b9a5e7d2929.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
*{
  font-family: 'Raleway', sans-serif;
}
#App{
  min-width: 100%;
}

.section{
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 150%;
}
.section button{
    font-size: 100%;
}
.column{
    display: flex;
    justify-content: center;
    margin-bottom: 1%;
}
.btn{
    padding: 0.25em 1.5em;
}
.box{
    background-image: linear-gradient(to top right, black, grey);
    border-radius: 2%;
    color: ghostwhite;
    padding: 0.25% 0;
}
.box-wrapper{
    background-image: linear-gradient(to top right, rgb(24, 18, 18), silver);
    padding: 0.55em;
    margin: 0% 1.5%;
    border-radius: 2.5%;
}
.line {
    display: flex;
    justify-content: center;
    text-align: center;
}
h5{
    padding: 0 1%;
}
h4{
    justify-self: center;
}
ul{
    list-style: none;
    padding-left: 0%;
}
.board{
    justify-content: center;
    width: 100%;
}
form{
    display: flex;
    align-items: center;
    margin-bottom: 2em;
    width: 100%;
}
.addPlayer{
    width: 50%;
}
.banner{
    background-color: #198754;
    color: #fff;
    width: 100%;
    margin: 0 0 5% 0;
}
.prompt{
    height: 100%;
}
.prompt li{
    color: white;
}
